import React, { useState } from 'react';
import CertificateItem from './CertificateItem'; // Componente para renderizar un certificado
import PaginationControls from './PaginationControls'; // Controles de paginacin

const Certificates = ({ certificates }) => {
  const [currentPage, setCurrentPage] = useState(1); // Control de paginacin
  const pageSize = 6; // Tamao de pgina para la paginacin

  const handleChangePage = (newPage) => {
    setCurrentPage(newPage); // Cambiar la pgina actual
  };

  const start = (currentPage - 1) * pageSize; // ndice de inicio de la pgina actual
  const end = start + pageSize; // ndice final de la pgina actual
  const paginatedCertificates = certificates.slice(start, end); // Certificados para la pgina actual

  return (
    <section id="certificates" className="sections">
      <h1>Certificaciones</h1>
      <div id='divcertificates'>
      {paginatedCertificates.map((certificate) => (
        <CertificateItem key={certificate.name} certificate={certificate} />
      ))}
      </div>
      <PaginationControls
        totalCertificates={certificates.length}
        currentPage={currentPage}
        onPageChange={handleChangePage}
      />
    </section>
  );
};

export default Certificates;
